
import { mapActions } from 'vuex'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Box from 'vux/src/components/box/index.vue'
import Range from 'vux/src/components/range/index.vue'
import XNumber from 'vux/src/components/x-number/index.vue'

export default {
components: {Group, Cell, XButton, Box, Range, XNumber},
data () {
return {
coinNum:0
}
},
mounted () {

},
methods:{
...mapActions(['generateCoin']),
doGenerateCoin () {
if(this.coinNum <= 0) {
this.$vux.toast.show({text:"数量错误",type:"cancel"})
return
}
this.generateCoin(this.coinNum).then(() => this.$vux.toast.show({text:"生成成功!"}))
this.coinNum = 0
}
}
}



