
import Panel from 'vux/src/components/panel/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Search from 'vux/src/components/search/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

import { mapState, mapActions } from 'vuex'
export default {
data () {
return {}
},
components: { Panel, Group, Search, Scroller, Cell },
methods: {
...mapActions([
"getPlayerList"
])
},
computed: {
...mapState({
list: state => state.user.playerList.items
}),
players () {
return this.list.map((player) => {
let title = player.wxNickname
if(player.id === this.$store.state.user.id) {
title += " (自己)"
}
let count = 0
if(player.orders) {
player.orders.map(o => {
if(o.status === 1) {
count += o.amount
}
})
}
return {
title:title,
src:player.wxHeadimgurl,
url:"/home/player/details/"+player.id,
orderCount:"¥"+count
}
})
}
}
}




