
import { mapState, mapActions } from 'vuex'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Range from 'vux/src/components/range/index.vue'
import XNumber from 'vux/src/components/x-number/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Box from 'vux/src/components/box/index.vue'
import XImg from 'vux/src/components/x-img/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Panel from 'vux/src/components/panel/index.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import XTable from 'vux/src/components/x-table/index.vue'
import Divider from 'vux/src/components/divider/index.vue'

export default {
components : { Group, Cell, Range, XNumber, XButton, Box, XImg, Panel, XInput, Grid, XTable, Divider },
data () {
return {

applyListPath: '/home/seller/ApplyList'
}
},
mounted () {

this.getSellerInfo()
},
computed: {
...mapState({
sellerInfo : state => state.user.sellerInfo
})
},
filters:{
DateFormat:function (time) {
return dateFormat(new Date(time),'yy-MM-dd hh:mm')
}
},
methods: {
...mapActions([
'getSellerInfo',
'applyCash'
]),
async onScrollBottom (args) {
this.$nextTick(() => {

})
},
async showCoinConfirm () {




if (this.sellerInfo.rebate <= 0) {
this.$vux.toast.show({text:"余额不足",type:"cancel"})
return
}

this.$vux.confirm.show({
title: "确认",
content: `你确认要申请提现 ￥ ${this.sellerInfo.rebate}吗？`,
onConfirm: () => {
this.applyCash()
.then(res => {
if(res.data.isSucc === 0) {
this.$vux.toast.show({text:"申请成功",type:"info"})

} else {
this.$vux.toast.show({text:res.data.msg,type:"cancel"})

}
})
.then(this.getSellerInfo)
}
})
},
showList () {
this.$router.push('/home/seller/ApplyList')
}
}
}
const dateFormat = function (date, fmt) {
let o = {
"M+": date.getMonth() + 1,
"d+": date.getDate(),
"h+": date.getHours(),
"m+": date.getMinutes(),
"s+": date.getSeconds(),
"q+": Math.floor((date.getMonth() + 3) / 3),
"S": date.getMilliseconds()
}
if (/(y+)/.test(fmt)) fmt = fmt.replace(RegExp.$1, (date.getFullYear() + "").substr(4 - RegExp.$1.length))
for (var k in o) {
if (new RegExp("(" + k + ")").test(fmt)) fmt = fmt.replace(RegExp.$1, (RegExp.$1.length === 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)))
}
return fmt
}



