
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import Loading from 'vux/src/components/loading/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import { mapState, mapActions } from 'vuex'
export default {
data () {
return {
}
},
directives: {
TransferDom
},
components: {
ButtonTab,
ButtonTabItem,
ViewBox,
XHeader,
Tabbar,
TabbarItem,
Loading
},
methods: {
reload () {
document.location.reload()
},
scrollTop () {
this.$refs.viewBox.scrollTo(0)
},
...mapActions([
'updateDemoPosition',
'getLinkList'
]),
setLocale (locale) {
this.$i18n.set(locale)
this.$locale.set(locale)
}
},
mounted () {
this.handler = () => {
if (this.path === '/demo') {
this.updateDemoPosition(this.$refs.viewBox.getScrollTop())
}
}
this.box = this.$refs.viewBox.getScrollBody()
if (this.path === '/demo') {
this.box.addEventListener('scroll', this.handler, false)
}






},
beforeDestroy () {
this.box.removeEventListener('scroll', this.handler, false)
},
watch: {
path (path) {
this.$nextTick(() => {
this.box.scrollTop = 0
})
/* if (path === '/component/demo') {
this.$router.replace('/demo')
return
}
if (path === '/demo') {
this.box.removeEventListener('scroll', this.handler, false)
this.box.addEventListener('scroll', this.handler, false)
} else {
this.box.removeEventListener('scroll', this.handler, false)
}
if (path === '/demo' && this.demoTop) {
this.$nextTick(() => {
setTimeout(() => {
this.box.scrollTop = this.demoTop
}, 550)
})
} else {
this.$nextTick(() => {
this.box.scrollTop = 0
})
} */
}
},
computed: {
...mapState({
route: state => state.route,
path: state => state.route.path,
deviceready: state => state.app.deviceready,
demoTop: state => state.vux.demoScrollTop,
isLoading: state => state.vux.isLoading,
direction: state => state.vux.direction
}),
leftOptions () {
return {
showBack: this.route.path !== '/'
}
},
headerTransition () {
return this.direction === 'forward' ? 'vux-header-fade-in-right' : 'vux-header-fade-in-left'
},
componentName () {
if (this.route.path) {
const parts = this.route.path.split('/')
if (/component/.test(this.route.path) && parts[2]) return parts[2]
}
},
title () {





},
tabbar () {
var list = [
{path:'/home',icon:'home',label:"首页",selected:/^\/home$/i},

{path:'/home/player/details',icon:'user-circle',label:"我",selected:/^\/home\/player/i}
]



return list
}
}
}



