import Vue from 'vue'
import VueRouter from 'vue-router'
import { sync } from 'vuex-router-sync'
import router from './router'
import App from './App'
import Vuex from 'vuex'
import store from './store'
import FastClick from 'fastclick'
import api from './plugins/api'
import lodash from './plugins/lodash'
import VCharts from 'v-charts'

import DevicePlugin from 'vux/src/plugins/device/index.js'
import ToastPlugin from 'vux/src/plugins/toast/index.js'
import AlertPlugin from 'vux/src/plugins/alert/index.js'
import ConfirmPlugin from 'vux/src/plugins/confirm/index.js'
import LoadingPlugin from 'vux/src/plugins/loading/index.js'
import WechatPlugin from 'vux/src/plugins/wechat/index.js'
import AjaxPlugin from 'vux/src/plugins/ajax/index.js'
import XInput from 'vux/src/components/x-input/index.vue'


Vue.use(VueRouter)
Vue.use(Vuex)
Vue.use(DevicePlugin)
Vue.use(ToastPlugin)
Vue.use(AlertPlugin)
Vue.use(ConfirmPlugin)
Vue.use(LoadingPlugin)
Vue.use(WechatPlugin)
Vue.use(AjaxPlugin)
Vue.use(XInput)
Vue.use(api)
Vue.use(lodash)
Vue.use(VCharts)

sync(store, router)

router.beforeEach(function ({path, meta, fullPath}, from, next) {
if (/^\/http/.test(fullPath)) {
fullPath = fullPath.substr(1)
window.location.href = fullPath
return
}

let { requiresAuth = true } = meta
let isLogin = !!store.state.user.id // true用户已登录， false用户未登录
if (requiresAuth && !isLogin) {
if (Vue.device.isWechat) {

return next({ path: '/wx/LoginWX' + location.search })
} else {
return next({ path: '/login' })
}
}
store.commit('updateLoadingStatus', {isLoading: true})
store.commit('updateDirection', {direction: 'forward'})
next()




})

router.afterEach(function (to) {
store.commit('updateLoadingStatus', {isLoading: false})
})


FastClick.attach(document.body)



/* eslint-disable no-new */
new Vue({
store,
router,
render: h => h(App)
}).$mount('#app-box')



